export default {
	props: {
		tabBars: Array,
		tabIndex: Number,
		scrollStyle:{
			type:String,
			default:''
		},
		scrollItemStyle:{
			type:String,
			default:''
		}
	},
	data() {
		return {};
	},
	onLoad() {},
	methods: {
		tabtap(index) {
			// this.tabIndex = index;
			//向父组件传值
			this.$emit('tabtap', index);
		}
	}
};
